section {
  position: absolute;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  background-color: #fbfdfd;
  width: 100%;
  min-height: 100%;
  font-family: Helvetica, sans-serif;
  padding-top: 56px; }
  section ul {
    position: relative;
    max-width: 1200px;
    margin: 50px auto 0; }
  section li {
    display: inline-block;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    width: 100%;
    padding: 15px; }
  section a {
    position: relative;
    display: block;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    -webkit-box-shadow: 0 10px 20px 0px rgba(0, 0, 0, 0.1);
    box-shadow: 0 10px 20px 0px rgba(0, 0, 0, 0.1);
    width: 100%;
    min-height: 150px;
    padding: 15px;
    background-color: #fff;
    text-decoration: none; }
  section h2 {
    color: #3d3d3e;
    font-size: 16px;
    margin-bottom: 5px; }
  section p {
    color: #5a5a5a;
    font-size: 14px; }
  section .date {
    position: absolute;
    left: 15px;
    bottom: 15px;
    color: #989899;
    font-size: 12px; }
  section .state {
    position: absolute;
    display: block;
    right: 15px;
    bottom: 15px;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: #67c956; }
    section .state.wip {
      background-color: #ed6960; }
  section .help {
    vertical-align: top;
    width: 150px; }
    section .help a {
      position: relative;
      color: #989899;
      -webkit-box-shadow: none;
      box-shadow: none;
      background: none;
      overflow: hidden;
      -webkit-perspective: 100px;
      perspective: 100px; }
      section .help a:hover .circle {
        -webkit-transform: translate(-50%, -50%) translateZ(70px);
        transform: translate(-50%, -50%) translateZ(70px); }
    section .help .letter,
    section .help .circle {
      position: absolute;
      width: 50px;
      height: 50px;
      padding: 20px;
      top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
      transform: translate(-50%, -50%); }
    section .help .letter {
      font-size: 50px;
      text-align: center; }
    section .help .circle {
      background-color: #f0f7f8;
      border-radius: 50%;
      -webkit-transition: -webkit-transform 0.2s;
      transition: -webkit-transform 0.2s;
      -o-transition: transform 0.2s;
      transition: transform 0.2s;
      transition: transform 0.2s, -webkit-transform 0.2s; }
  @media (min-width: 768px) {
    section li {
      width: 50%; } }
  @media (min-width: 992px) {
    section li {
      width: 25%; } }
